.edit-popup {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 70%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  background-color: #3498DB;
  padding: 40px;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  z-index: 1000;
  transition: transform 0.3s ease, opacity 0.3s ease;
  opacity: 1;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 15px;
  background-color: #e74c3c;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

.close-button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
}

.submit-btn {
  width: 45%;
  background-color: #ecf0f1;
  border-radius: 8px;
  text-align: center;
  margin: 20px auto;
  border: none;
  padding: 15px;
  cursor: pointer;
  font-weight: bold;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.submit-btn:hover {
  transform: scale(1.05);
  background-color: green;
}

.form-container {
  flex: 1 1 50%;
  padding: 20px;
  text-align: left; 
  margin: auto;
}

.form-container > label {
  display: block;
  font-size: 1.1em;
  margin-top: 15px;
  margin-bottom: 5px;
  color: #fff;
}

.form-container > input,
.form-container > select {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border-radius: 8px;
  border: 1px solid #ddd;
  background-color: #fff;
  box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: background-color 0.2s ease, box-shadow 0.2s ease;
}

.form-container > input:focus,
.form-container > select:focus {
  background-color: #f0f8ff;
  box-shadow: inset 0px 3px 6px rgba(0, 0, 0, 0.15);
}

.form-container {
  display: flex;
  flex-wrap: wrap; 
  gap: 20px; 
}

@media (max-width: 768px) {
  .creation-popup {
      width: 90%;
      height: auto;
  }
  
  .form-container {
      flex-direction: column; /* Stack form containers on smaller screens */
  }

  #submit {
      width: 100%; /* Full width button on small screens */
  }
}